import { net } from '@/service/api/net.js'
import config from '@/common/config'
import conn_util from '@/common/es/conn_util'

import axios from 'axios'

let url = config.baseURL + '/into/'
const _url = url 

export default {
  url,
  login(n, p) {
    const data = {
      'into_k': n,
      'into_v': p,
      'salt': 'DENGLU'
    }
    return axios.post(url, JSON.stringify(data))
  }
}


