import db from '../tools/storage'
import salt from '../scret/salt'


const getAuth = async function() {
  let user = await db._getAsync('invoice_user', true)
  // user = JSON.parse(user)
  
  return {
    username: user.nick,
    password: salt.remove_salt_form_string(user.pass)
  }
}

export default {
  getAuth
}


