<template>
  
  <vue-html2pdf
      :show-layout="false"
      :float-layout="true"
      :enable-download="true"
      :preview-modal="true"
      :paginate-elements-by-height="1400"
      filename="導出PDF"
      :pdf-quality="0.5"
      :manual-pagination="false"
      pdf-format="a4"
      pdf-orientation="portrait"
      pdf-content-width="100%"

      :html-to-pdf-options="options"

      ref="html2Pdf"
  >
    <section slot="pdf-content" v-html="html">
        <!-- PDF Content Here -->
    </section>
  </vue-html2pdf>

</template>

<script>
import VueHtml2pdf from 'vue-html2pdf'

import tool from '@/common/es/tool'

export default {
  name: 'PdfVueHtml',
  components: {

    VueHtml2pdf
  },
  data() {
    return {
      html: '',
      options: {

        image: {
            type: 'jpeg', 
            quality: 0.5
        },
      }
    }
  },
  methods: {

    getHtml() {
      const h = tool.html('pdf_dom')
      return h
    },
    print() {
      
      this.html = this.getHtml()
      
      this.$refs.html2Pdf.generatePdf()
    }
  }
}
</script>

<style>

</style>


