import { net } from '@/service/api/net.js'
import config from '@/common/config'

import tool from '@/common/es/tool'

import conn_util from '@/common/es/conn_util'

let url = config.baseURL + config.apiURL + '/listing_no/'
const _url = url 

export default {
  url,
  async getTodayNumed() {
    let n = 1
    let res = ''
    const condition = {
      start_timed: tool.timed.getNumedTimed('-'),
      end_timed: tool.timed.getNumedTimed('-', 1)
    }

    url = _url + conn_util.buildURL(condition, true)
    let nos = await net({ url, method: 'GET' })
    if (nos) {
      nos = nos.filter(e => e.used == true)
      
      if (nos.length > 0) { n = nos.length + 1 }
      res = tool.timed.getNumedTimed() + tool.salt.full_number(n, 4)
    }

    return res
  },
  getList(condition, status) {

    url = _url + conn_util.buildURL(condition, status)
    
    return net({
      url,
      method: 'GET'
    })
  },
  
  async trashByFilter(condition) {
    url = _url + conn_util.buildURL(condition, true)
    const res = await net({ url, method: 'GET' })
    
    if (res && res.length > 0) {
      url = _url + res[0].id + '/'
      await net({ url, method: 'PUT', data: { 'status': 'False' } })
    }
  },
  
  trash(id) {
    url = _url + id + '/'
    console.log(url, '删除')
    return net({
      url,
      method: 'PUT',
      data: {
        status: 'False'
      }
    })
  },
  add(data) {
    url = _url
    
    return net({
      url,
      method: 'POST',
      data
    })
  },
  update(data, id) {
    url = _url + id + '/'

    return net({
      url,
      method: 'PUT',
      data
    })
  },
  getListPage(_url) {
    return net({
      url: _url,
      method: 'get'
    })
  }
}


